@import url('https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap');

.header {
  width: 100%;
  height: 60px;
  font-size: 18px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}


.header ul {    
  list-style-type: none;
}

.header li {
  position: relative;
  margin: 5px 15px;
  
}

.header a {
  text-decoration: none;
  color: #291948;
  font-family: 'New Tegomin', serif;
text-shadow: #000000;
}
.header a:hover {
 color: #000000;
}


.home-page {
  display:flex;
  flex-direction: column;
  margin-top: 30px;
}

.photos {
  margin: 6%;
  border: 1px solid;
  border-radius: 15px;
  padding: 20px;
}

.photos img {
  width: 230px;
  height: auto;
  margin: 15px;
  border-radius: 5px;
  cursor: pointer;
}