@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  width: 100%;
  height: 60px;
  font-size: 18px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;  
}


.header ul {    
  list-style-type: none;
}

.header li {
  position: relative;
  margin: 5px 15px;
  
}

.header a {
  text-decoration: none;
  color: #291948;
  font-family: 'New Tegomin', serif;
text-shadow: #000000;
}
.header a:hover {
 color: #000000;
}


.home-page {
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 30px;
}

.photos {
  margin: 6%;
  border: 1px solid;
  border-radius: 15px;
  padding: 20px;
}

.photos img {
  width: 230px;
  height: auto;
  margin: 15px;
  border-radius: 5px;
  cursor: pointer;
}
*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
  }
  
  
  .post-feed {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 10px;
    gap: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
    width: 1000px;
  }
.post-container {
    width: 600px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 15px;
    border: 1px solid;
    border-radius: 10px;
    background-color: #ffffff;
}

.user-avatar {
  min-width: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  border-radius: 50%;
    width: 50px;
    height: 50px;
}

.user-avatar img {
  width: 55%;
  height: 100%;
  border-radius: 50%;
}


.post-body {
  padding-right: 12.5px;
  font-size: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 20px;
}
.user-info{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: baseline;
          align-items: baseline;
  grid-gap: 15px;
  gap: 15px;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}

.user-name {
  font-weight: bold;
}


.post-text {
  font-family: 'Montserrat', sans-serif;
  margin-top: 15px;
  width: 100%;
}

.post-image {
  border-radius: 15px;
  width: 100%; 
  display: block;
  margin-top: 15px;
}
html {
  font-size: 62.5%; }
body {
  font-size: 1.5em;
  line-height: 1.6;
  font-weight: 400;
  color: #222;
  font-family: 'Pacifico', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: 'Pacifico', cursive;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  font-family: 'Pacifico', cursive;

}
body {
  background-color: #545454af;
}
.App input {
  height: 38px;
  padding: 0 30px;
  color: #555;
  text-align: center;
  font-size: 11px;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: #ffffff77;
  border-radius: 4px;
  border: 1px solid #bbb;
  cursor: pointer;
  box-sizing: border-box;
}
p {
  margin: 4%;
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  -webkit-align-items: center;
  align-items: center; 
  position: relative;
  width: 100%;
  max-width: 960px;
  box-sizing: border-box;
  padding: 1%;
  margin: 5%;
  background-color: #fff;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  font-size: 18px;
  color: #000000;
}

